import React, { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router';
import { InputGroupInline, WindowComponent, WindowComponentProps } from 'summer';
import { getDefaultPrinterDataBasic, PrinterDataBasic } from '../../../../../../models/dtos/ger/printerDataBasic';
import { SIMPLE } from '../../../../../../models/enumerated/ger/printerConfigTypeEnum';
import { Reducers } from '../../../../../../store/ducks';
import { GlobalState } from '../../../../../../store/ducks/global.duck';
import ReportFormView from '../../../../report/ReportFormView';
import { CiapRelPass, CLASS_KEY, REPORT_EVENT, REPORT_TITLE } from '../ciapRelRV';

const BASIC_DATA: PrinterDataBasic = {
  ...getDefaultPrinterDataBasic(REPORT_TITLE, REPORT_EVENT, CLASS_KEY, SIMPLE.value),
  module: 'MLF',
};

export default function CiapExampleReportView(props: WindowComponentProps) {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const history = useHistory();
  const formProps = useForm<CiapRelPass>({ mode: 'onBlur' });

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresa) {
        setTimeout(() => {
          formProps.setValue('idGerEmpresa', globalReducer.globalParameter.empresa.id);
          formProps.triggerValidation('idGerEmpresa');
          formProps.setValue('modelo', true);
          formProps.triggerValidation('modelo');
          formProps.setValue('armazenarPaginaInicial', false);
          formProps.triggerValidation('armazenarPaginaInicial');
        }, 1000);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent {...props} title={REPORT_TITLE}>
      <ReportFormView formProps={formProps} printerData={BASIC_DATA} history={history} extraConfigOpened={true}>
        <InputGroupInline formProps={formProps} id="idGerEmpresa" hidden={true} />
        <InputGroupInline formProps={formProps} id="modelo" hidden={true} />
        <InputGroupInline formProps={formProps} id="armazenarPaginaInicial" hidden={true} />
      </ReportFormView>
    </WindowComponent>
  );
}
